import api from '@/api/api.js';
import util from '@/common/util.js';
import modal from "@/components/modal/modal";
export default{
	components: {
		modal
	},
	data(){
		return {
			fr_name:'',
			fr_img:'',
			modal9: false,
			info:''
		}
	},
	onLoad(res){
	    this.fr_name = res.account;
		this.fr_img = res.img;
	},
	methods:{
		hide9() {
			this.modal9 = false
		},
		apply(){
			this.modal9 = true;
			this.info = '';
		},
		send_info(){
			this.modal9 = false;
			uni.showLoading({
				title:"正在发送....."
			})
			let postData = {
				account: this.fr_name,
				fromAccount: this.$store.state.userName,
				msg: this.info				
			};							
			this.applyPass(postData);
		},
		applyPass(postData){
			api.applyFriend(postData, res => {
				let code = api.getCode(res);
				let msg = api.getMsg(res); 
				uni.hideLoading();
                if(code === 0){
					uni.showToast({
						title: msg,
						image:'/static/img/check-circle.png',
						duration:2000
					})
				}else {
					uni.showToast({
						title: "网络出错请稍候再试！",
						image:'/static/img/fail-circle.png',
						duration:2000
					})
				}
			})
		}
	}
}
